export default class Paddle {
  constructor(gameWidth, gameHeight) {
    this.gameWidth = gameWidth;
    this.gameHeight = gameHeight;
    this.maxSpeedY = 6.5;
    this.maxSpeedX = 6;
    this.speedy = 0;
    this.speedx = 0;
    this.doublejump = 0;
    this.PD = 0;
    this.shoot = 0;
    this.speedxP = 0;
    this.speedxP2 = 0;
    this.timer = 0;
    this.speedxG = 0;
    this.score = 0;
    this.random = 0;
    this.Eshoot = 0;
    this.OXPL = [
      1549.5,
      1599.5,
      1649.5,
      1699.5,
      1749.5,
      1799.5,
      1849.5,
      1899.5,
      1949.5,
      1999.5,
      2049.5,
      2099.5,
      2149.5,
      2199.5
    ];
    this.PlayerHealth = 3;
    this.position = {
      y: 570,
      x: gameWidth / 2 - 50,
      BGX: 0,
      BGX2: 0,
      OX: 850,
      GX: 1100,
      enemyProjectileX: 1150,
      enemyProjectileY: 620,
      enemyProjectileX2: 1150,
      enemyProjectileY2: 620,
      FPx: 1500,
      FPy: 470,
      LPx: 2000,
      LPy: 670,
      FogX: 1368,
      MBx: 1550,
      MBy: 270,
      BX: 1500,
      EX: 1500,
      EY: 137.5,
      Cx: 1900
    };
  }

  moveLeft() {
    if (
      this.position.x + 65 < this.position.OX ||
      this.position.x + 25 > this.position.OX + 150 ||
      this.position.y + 100 < 520
    ) {
      if (this.position.x > -15) {
        this.speedx = -this.maxSpeedX;
        this.PD = 2;
      } else {
        this.PD = 0;
      }
    }
  }

  moveRight() {
    if (
      this.position.x + 75 < this.position.OX ||
      this.position.x + 35 > this.position.OX + 150 ||
      this.position.y + 100 < 520
    ) {
      if (this.position.x < this.gameWidth / 2 - 50) {
        this.speedx = this.maxSpeedX;
        this.PD = 1;
      } else {
        this.speedx = 0;
        this.PD = 1;
      }
    } else {
      this.speedx = 0;
    }
  }

  jump() {
    if (
      this.position.y > 569 ||
      this.doublejump == 1 ||
      (this.position.x + 75 > this.position.OX &&
        this.position.x + 25 < this.position.OX + 150 &&
        this.position.y + 100 < 520 &&
        this.position.y + 100 > 518) ||
      (this.position.x + 75 > this.position.FPx &&
        this.position.x + 25 < this.position.FPx + 150 &&
        this.position.y == this.position.FPy - 100)
    ) {
      this.speedy = -this.maxSpeedY;
      this.doublejump += 1;
    }
  }

  stopxR() {
    this.PD = 0;
    if (this.speedx > 0) {
      this.speedx = 0;
      if (this.position.x == this.gameWidth / 2 - 100) {
        this.position.x = this.gameWidth / 2 - 50;
      }
    }
  }

  stopxL() {
    if (this.speedx < 0) {
      this.speedx = 0;
      this.PD = 0;
    }
  }

  draw(ctx) {
    var MCFront = document.getElementById("MarioF");
    var MCRight = new Image();
    MCRight.src = "images/MCRight.png";
    var MCLeft = new Image();
    MCLeft.src = "images/MCLeft.png";
    var BGImg = new Image();
    BGImg.src = "images/BGC.png";
    var BGImg2 = new Image();
    BGImg2.src = "images/BGS.jpg";
    var OG = new Image();
    OG.src = "images/ground.jpg";
    var OGG = new Image();
    OGG.src = "images/grassGround.jpg";
    var EH = new Image();
    EH.src = "images/EmptyHealth.png";
    var FH = new Image();
    FH.src = "images/fullHealth.png";
    var GOOMBA = new Image();
    GOOMBA.src = "images/GOOM.png";
    var FB = new Image();
    FB.src = "images/FireB.png";
    var LP = new Image();
    LP.src = "images/lavaPit.jpg";
    var FOG = new Image();
    FOG.src = "images/fog.png";
    var MB = new Image();
    MB.src = "images/MysteryBlock.png";
    var BIRD = new Image();
    BIRD.src = "images/bird.png";
    var EGG = new Image();
    EGG.src = "images/Egg.png";
    var COIN = new Image();
    COIN.src = "images/coin.png";
    ctx.drawImage(BGImg2, this.position.BGX2, 0, 1368, 670);
    ctx.drawImage(BGImg2, this.position.BGX2 + 1367, 0, 1368, 670);
    ctx.drawImage(BGImg, this.position.BGX, 0, 1368, 670);
    ctx.drawImage(BGImg, this.position.BGX + 1368, 0, 1368, 670);
    if (this.speedx == 0) {
      if (this.PD == 0) {
        ctx.drawImage(MCFront, this.position.x, this.position.y, 100, 100);
      }
    }
    if (this.speedx > 0 || this.PD == 1) {
      ctx.drawImage(MCRight, this.position.x, this.position.y, 100, 100);
    }
    if (this.speedx < 0 || this.PD == 2) {
      ctx.drawImage(MCLeft, this.position.x, this.position.y, 100, 100);
    }
    //Goomba Projectile
    ctx.drawImage(
      FB,
      this.position.enemyProjectileX - 20,
      this.position.enemyProjectileY,
      40,
      40
    );
    ctx.drawImage(
      FB,
      this.position.enemyProjectileX2 - 20,
      this.position.enemyProjectileY2,
      40,
      40
    );
    //Coin
    ctx.drawImage(COIN, this.position.Cx, 620, 50, 50);
    //Goomba
    ctx.drawImage(GOOMBA, this.position.GX, 570, 100, 100);
    //Bird
    ctx.drawImage(EGG, this.position.EX + 37.5, this.position.EY, 25, 25);
    ctx.drawImage(BIRD, this.position.BX, 100, 100, 100);
    //Obstical
    ctx.drawImage(OG, this.position.OX, 670, 50, 50);
    ctx.drawImage(OG, this.position.OX + 50, 670, 50, 50);
    ctx.drawImage(OG, this.position.OX + 100, 670, 50, 50);
    //new layer
    ctx.drawImage(OG, this.position.OX, 620, 50, 50);
    ctx.drawImage(OG, this.position.OX + 50, 620, 50, 50);
    ctx.drawImage(OG, this.position.OX + 100, 620, 50, 50);
    //new layer
    ctx.drawImage(OG, this.position.OX, 570, 50, 50);
    ctx.drawImage(OG, this.position.OX + 50, 570, 50, 50);
    ctx.drawImage(OG, this.position.OX + 100, 570, 50, 50);
    //new layer
    ctx.drawImage(OGG, this.position.OX, 520, 50, 50);
    ctx.drawImage(OGG, this.position.OX + 50, 520, 50, 50);
    ctx.drawImage(OGG, this.position.OX + 100, 520, 50, 50);
    //Floating Platform
    ctx.drawImage(OGG, this.position.FPx, this.position.FPy, 50, 50);
    ctx.drawImage(OGG, this.position.FPx + 50, this.position.FPy, 50, 50);
    ctx.drawImage(OGG, this.position.FPx + 100, this.position.FPy, 50, 50);
    ///Lava Pit
    ctx.drawImage(LP, this.position.LPx, this.position.LPy, 150, 100);
    ctx.drawImage(MB, this.position.MBx, this.position.MBy, 50, 50);
    //health bar
    ctx.drawImage(EH, 50, 50, 180, 60);
    if (this.PlayerHealth == 3) {
      ctx.drawImage(FH, 50, 50, 60, 60);
      ctx.drawImage(FH, 110, 50, 60, 60);
      ctx.drawImage(FH, 170, 50, 60, 60);
    } else if (this.PlayerHealth == 2) {
      ctx.drawImage(FH, 50, 50, 60, 60);
      ctx.drawImage(FH, 110, 50, 60, 60);
    } else if (this.PlayerHealth == 1) {
      ctx.drawImage(FH, 50, 50, 60, 60);
    }
    ctx.drawImage(FOG, this.position.FogX, 0, 1368, 770);
    ctx.drawImage(FOG, this.position.FogX - 1368, 0, 1368, 770);
    ctx.drawImage(FOG, this.position.FogX + 1368, 0, 1368, 770);
    if (this.PlayerHealth <= 0) {
      ctx.fillStyle = "red";
      ctx.fillRect(0, 0, 1368, 770);
      ctx.fillStyle = "blue";
      ctx.fillRect(
        this.gameWidth / 2 - 200,
        this.gameHeight / 2 - 100,
        400,
        200
      );
      ctx.fillStyle = "black";
      ctx.font = "60px Georgia";
      ctx.fillText("You're Dead", 521, 400);
    }
    ctx.fillStyle = "white";
    ctx.font = "30px Georgia";
    ctx.fillText(" Player Score: " + this.score, 578, 110);
  }

  update(deltaTime, e) {
    if ((!deltaTime, e)) return;
    this.position.x += this.speedx;
    this.position.y += this.speedy;
    this.position.enemyProjectileX += this.speedxP;
    this.position.enemyProjectileX2 += this.speedxP2;
    this.position.GX += this.speedxG;
    if (this.position.y < 570) {
      if (
        this.position.x + 65 < this.position.OX ||
        this.position.x + 35 > this.position.OX + 150 ||
        this.position.y + 100 < 519
      ) {
        if (
          this.position.x + 65 < this.position.FPx ||
          this.position.x + 35 > this.position.FPx + 150 ||
          this.position.y + 100 < this.position.FPy ||
          this.position.y + 100 > this.position.FPy + 50
        ) {
          this.speedy += 0.2;
        } else {
          if (this.position.y + 100 < this.position.FPy + 50) {
            this.position.y = this.position.FPy - 100;
          }
          this.speedy = 0;
          this.doublejump = 0;
        }
      } else {
        this.position.y = 419;
        this.speedy = 0;
        this.doublejump = 0;
      }
    } else {
      this.position.y = 570;
      this.speedy = 0;
      this.doublejump = 0;
    }

    if (this.position.x == this.gameWidth / 2 - 50) {
      this.speedx = 0;
      if (
        this.position.x + 75 < this.position.OX ||
        this.position.x + 25 > this.position.OX + 150 ||
        this.position.y + 100 < 520
      ) {
        if (
          this.position.x + 65 < this.position.FPx ||
          this.position.x + 35 > this.position.FPx + 150 ||
          this.position.y + 100 < this.position.FPy + 6 ||
          this.position.y > this.position.FPy + 50
        ) {
          if (this.PD == 1) {
            document.getElementById("gameScreen").style.backgroundPositionX =
              parseInt(
                document.getElementById("gameScreen").style
                  .backgroundPositionX || 0,
                10
              ) -
              6 +
              "px";
            this.position.BGX -= 2;
            this.position.BGX2 -= 1;
            this.position.OX -= 6;
            this.position.GX -= 6;
            this.position.enemyProjectileX -= 6;
            this.position.enemyProjectileX2 -= 6;
            this.position.FPx -= 6;
            this.position.LPx -= 6;
            this.position.MBx -= 6;
            this.position.FogX -= 7;
            this.position.BX -= 6;
            this.position.EX -= 6;
            this.position.Cx -= 6;
          }
        }
      }
    }
    if (this.position.x < -15) {
      this.speedx = 0;
    }
    if (this.position.BGX < -1368) {
      this.position.BGX = 0;
    }
    if (this.position.BGX2 < -1368) {
      this.position.BGX2 = 0;
    }
    if (this.position.x + 75 > this.position.OX) {
      if (this.position.x + 25 < this.position.OX + 150) {
        if (this.position.y + 100 > 520) {
          this.speedx = 0;
        }
      }
    }
    if (this.position.OX <= -150) {
      this.position.OX += this.OXPL[Math.floor(Math.random() * (14 - 0) + 0)];
    }
    if (this.position.FPx <= -150) {
      this.position.FPx =
        this.position.OX + this.OXPL[Math.floor(Math.random() * (14 - 0) + 0)];
      this.random = Math.floor(Math.random() * (3 - 0) + 0);
      if (this.random == 1) {
        this.position.MBy = 270;
      } else {
        this.position.MBy = 1000;
      }
    }
    if (this.position.LPx <= -150) {
      this.position.LPx =
        this.position.OX + this.OXPL[Math.floor(Math.random() * (14 - 0) + 0)];
    }
    if (
      this.position.y > 568 &&
      this.position.GX + 50 < 1368 &&
      this.position.GX + 50 > 0 &&
      this.shoot == 0
    ) {
      if (
        this.position.x + 50 < this.position.GX + 50 &&
        (this.position.OX + 150 < this.position.x + 50 ||
          this.position.OX > this.position.GX + 50)
      ) {
        this.speedxP = -8;
        this.shoot = 1;
      }
      if (
        this.position.x + 50 > this.position.GX + 50 &&
        (this.position.OX > this.position.x + 50 ||
          this.position.OX < this.position.GX + 50)
      ) {
        this.speedxP = 8;
        this.shoot = 1;
      }
    }
    if (this.shoot == 1) {
      this.timer += 1;
    }
    if (this.timer > 200) {
      this.shoot = 0;
      this.timer = 0;
      this.speedxP = 0;
      this.position.enemyProjectileX = this.position.GX + 50;
      this.position.enemyProjectileY = 620;
    }
    if (
      this.position.enemyProjectileX + 20 > this.position.OX &&
      this.position.enemyProjectileX - 20 < this.position.OX + 150
    ) {
      this.speedxP = 0;
      this.position.enemyProjectileX = this.position.GX + 50;
      this.position.enemyProjectileY = 620;
    }
    if (
      this.position.enemyProjectileX2 + 20 > this.position.OX &&
      this.position.enemyProjectileX2 - 20 < this.position.OX + 150
    ) {
      this.speedxP2 = 0;
      this.position.enemyProjectileX2 = this.position.GX + 50;
      this.position.enemyProjectileY2 = 620;
    }
    if (
      Math.sqrt(
        Math.pow(this.position.x + 50 - this.position.enemyProjectileX, 2) +
          Math.pow(this.position.y + 50 - this.position.enemyProjectileY, 2)
      ) < 45
    ) {
      if (this.position.y > 568) {
        this.speedxP = 0;
        this.position.enemyProjectileX = this.position.GX + 50;
        this.position.enemyProjectileY = 620;
        this.PlayerHealth -= 1;
      } else {
        if (this.position.enemyProjectileX == this.position.GX + 50) {
          this.position.enemyProjectileX += 2000;
          this.position.GX += 2000;
          this.score += 3;
          this.position.enemyProjectileX2 = this.position.GX + 50;
          this.position.enemyProjectileY2 = 620;
          this.speedxP = 0;
          this.speedxP2 = 0;
        }
      }
    }
    if (
      this.position.enemyProjectileX < this.position.x + 50 &&
      this.speedxP < 0
    ) {
      this.speedxP2 = this.speedxP;
      this.position.enemyProjectileX2 = this.position.enemyProjectileX;
      this.position.enemyProjectileY2 = this.position.enemyProjectileY;
      this.speedxP = 0;
      this.position.enemyProjectileX = this.position.GX + 50;
      this.position.enemyProjectileY = 620;
    }
    if (
      this.position.enemyProjectileX > this.position.x + 50 &&
      this.speedxP > 0
    ) {
      this.speedxP2 = this.speedxP;
      this.position.enemyProjectileX2 = this.position.enemyProjectileX;
      this.position.enemyProjectileY2 = this.position.enemyProjectileY;
      this.speedxP = 0;
      this.position.enemyProjectileX = this.position.GX + 50;
      this.position.enemyProjectileY = 620;
    }
    if (
      this.position.GX + 100 > this.position.OX &&
      this.position.GX < this.position.OX + 150
    ) {
      this.position.GX += 150;
      this.position.enemyProjectileX -= 150;
      this.position.enemyProjectileX2 -= 150;
    }
    if (this.position.GX + 50 < -50 && this.shoot == 0) {
      this.position.GX = this.position.x + 1850;
      this.position.enemyProjectileX = this.position.x + 1900;
      this.position.enemyProjectileX2 = this.position.x + 1900;
    }
    if (
      this.position.x + 25 > this.position.GX + 50 &&
      (this.position.OX - 50 > this.position.x + 50 ||
        this.position.OX < this.position.GX + 50)
    ) {
      this.speedxG = 3;
    } else {
      if (this.speedxG == 3) {
        this.speedxG = 0;
      }
    }
    if (
      this.position.x + 75 < this.position.GX + 50 &&
      (this.position.OX + 200 < this.position.x + 50 ||
        this.position.OX > this.position.GX + 50)
    ) {
      this.speedxG = -3;
    } else {
      if (this.speedxG == -3) {
        this.speedxG = 0;
      }
    }
    if (this.speedxP == 0) {
      this.position.enemyProjectileX = this.position.GX + 50;
    }
    if (this.speedxP2 == 0) {
      this.position.enemyProjectileX2 = this.position.GX + 50;
    }
    if (
      this.position.LPx + 150 > this.position.OX - 50 &&
      this.position.LPx < this.position.OX + 200
    ) {
      this.position.LPx += 500;
    }
    if (
      this.position.FPx + 150 > this.position.OX - 50 &&
      this.position.FPx < this.position.OX + 200
    ) {
      this.position.FPx += 500;
    }
    if (
      this.position.x + 75 > this.position.FPx &&
      this.position.y + 100 > this.position.FPy &&
      this.position.x + 25 < this.position.FPx + 150 &&
      this.position.y < this.position.FPy + 50
    ) {
      if (this.position.y + 100 < this.position.FPy + 5) {
        this.doublejump = 0;
      } else {
        this.speedx = 0;
      }
    }
    if (
      this.position.x + 75 > this.position.FPx &&
      this.position.x + 25 < this.position.FPx + 150
    ) {
      if (this.position.y < 525 && this.position.y > 510) {
        this.speedy = 3;
      }
    }
    if (
      this.position.y > 568 &&
      this.position.x + 75 > this.position.LPx &&
      this.position.x + 25 < this.position.LPx + 150
    ) {
      this.PlayerHealth = 0;
    }
    if (
      this.position.GX + 75 > this.position.LPx &&
      this.position.GX + 25 < this.position.LPx + 150
    ) {
      this.position.GX =
        this.position.OX + this.OXPL[Math.floor(Math.random() * (14 - 0) + 0)];
    }
    if (this.position.FogX <= -1368) {
      this.position.FogX = 1368;
    }
    this.position.MBx = this.position.FPx + 50;
    if (
      ((this.position.x + 75 > this.position.MBx &&
        this.position.x + 75 < this.position.MBx + 50) ||
        (this.position.x + 25 > this.position.MBx &&
          this.position.x + 25 < this.position.MBx + 50)) &&
      ((this.position.y + 100 > this.position.MBy &&
        this.position.y + 100 < this.position.MBy + 50) ||
        (this.position.y > this.position.MBy &&
          this.position.y < this.position.MBy + 50))
    ) {
      this.position.MBy = 1000;
      if (this.PlayerHealth < 3) {
        this.PlayerHealth += 1;
      }
    }
    if (
      this.position.x + 25 < this.position.OX + 150 &&
      this.position.x + 75 > this.position.OX + 10 &&
      this.position.y > 568 &&
      this.PD != 2
    ) {
      document.getElementById("gameScreen").style.backgroundPositionX =
        parseInt(
          document.getElementById("gameScreen").style.backgroundPositionX || 0,
          10
        ) -
        6 +
        "px";
      this.position.BGX -= 2;
      this.position.BGX2 -= 1;
      this.position.OX -= 6;
      this.position.GX -= 6;
      this.position.enemyProjectileX -= 6;
      this.position.enemyProjectileX2 -= 6;
      this.position.FPx -= 6;
      this.position.LPx -= 6;
      this.position.MBx -= 6;
      this.position.FogX -= 7;
      this.position.BX -= 6;
      this.position.EX -= 6;
      this.position.Cx -= 6;
    }
    //Bird
    if (this.Eshoot == 0) {
      this.position.EX = this.position.BX;
    }
    this.position.BX -= 5;
    if (this.position.BX < -100) {
      this.position.BX = Math.random() * (6500 - 3000) + 3000;
    }
    if (
      this.position.EX + 12.5 > this.position.x - 10 &&
      this.position.EX + 12.5 < this.position.x + 10 &&
      this.speedx == 0
    ) {
      this.Eshoot = 1;
    }
    if (
      this.position.EX + 12.5 > this.position.x - 10 &&
      this.position.EX + 12.5 < this.position.x + 420 &&
      (this.speedx > 0 ||
        (this.position.x == this.gameWidth / 2 - 50 && this.PD == 1))
    ) {
      this.Eshoot = 1;
    }
    if (this.Eshoot == 1) {
      this.position.EY += 6;
    }
    if (this.position.EY > 645) {
      this.Eshoot = 0;
      this.position.EX = this.position.BX;
      this.position.EY = 137.5;
    }
    if (
      this.position.EX + 50 > this.position.x &&
      this.position.EX + 50 < this.position.x + 100 &&
      this.position.EY + 50 > this.position.y &&
      this.position.EY + 50 < this.position.y + 100
    ) {
      this.Eshoot = 0;
      this.position.EX = this.position.BX;
      this.position.EY = 137.5;
      this.PlayerHealth -= 1;
    }
    //Coin
    if (
      this.position.y > 568 &&
      this.position.x + 50 > this.position.Cx &&
      this.position.x + 50 < this.position.Cx + 50
    ) {
      this.position.Cx += Math.random() * (2500 - 1600) + 1600;
      this.score += 1;
    }
    if (
      (this.position.Cx + 25 > this.position.OX &&
        this.position.Cx + 25 < this.position.OX + 150) ||
      (this.position.Cx + 25 > this.position.LPx &&
        this.position.Cx + 25 < this.position.LPx + 150)
    ) {
      this.position.Cx += 200;
    }
    if (this.position.Cx < -50) {
      this.position.Cx += Math.random() * (2500 - 1600) + 1600;
    }
  }
}
